body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:"";content:none}table{border-collapse:collapse;border-spacing:0}html{scroll-behavior:smooth;height:100%}body{background:url(/landing-page/static/media/bg-1.1b76f9e4.jpg),linear-gradient(black, transparent 22%);background-repeat:repeat, no-repeat;background-blend-mode:overlay;font-family:"Roboto",sans-serif;color:#eee;min-height:100%;max-width:100%}.section-one{width:100%;display:flex;flex-direction:column;justify-content:flex-start;align-items:center}.section-two{display:flex;justify-content:space-between;padding:20px}.footer{font-family:"Roboto",sans-serif;background-color:rgba(59,59,59,0.2);height:20vh;display:flex;flex-direction:column;justify-content:center;text-align:center;font-size:1.5rem;color:#ddd}.newsletter #email,.newsletter #submit-email{align-self:center;font-family:"Roboto",sans-serif;height:25px;margin:10px 0;padding:0.5rem;border-style:none;border-radius:3px;font-size:1rem;height:auto}.newsletter #email{width:300px;border-top-right-radius:0;border-bottom-right-radius:0;border:1px solid #005aa7;opacity:0.8}.newsletter #email:focus{outline:none;opacity:1}.newsletter #submit-email{cursor:pointer;border-top-left-radius:0;border-bottom-left-radius:0;background-color:#005aa7;border:1px solid #005aa7;color:#fff}#navbar{display:flex;justify-content:space-around;align-items:center;width:100%;font-size:calc(10px + 2vmin);padding:2rem 0}#navbar li{display:inline}#navbar li a{padding:0.9rem 1rem}#navbar a,#navbar a:visited{text-decoration:none;color:#eee;transition:0.3s}#navbar a:hover{color:#60b2ff}.company{display:flex;align-items:center;font-family:"Oxanium",sans-serif;font-size:2rem}.company .logo-container{width:80px;height:80px;border-radius:100%;overflow:hidden}.company .logo-container img{-webkit-transform:scale(1.35);transform:scale(1.35);object-position:center}.company p{margin-left:15px}.company p::after{content:'game on';margin:5px 0 0 5px;display:block;font-size:1rem;color:#ffe657}#section-one-content{width:100%;display:flex;justify-content:center;align-items:flex-start;padding:20px;box-sizing:border-box;position:relative}#banner-black-friday{width:550px;-webkit-transform:translate(-35%, -5%);transform:translate(-35%, -5%);position:absolute;z-index:-1;left:22%;top:5%;-webkit-filter:hue-rotate(210deg);filter:hue-rotate(210deg)}#main-deals{height:100%;margin:0 2rem;display:flex;flex-direction:column;justify-content:flex-start;align-items:center;text-align:center;-webkit-transform:translateX(30%);transform:translateX(30%)}#headline{color:#ffe657;width:100%;font-size:3.5rem;text-align:center;padding-bottom:2rem}.main-sales{width:100%;display:flex;justify-content:center;align-items:center}#section-two-deals{width:80vw;border-radius:5px;display:flex;justify-content:space-around;align-items:center;flex-wrap:wrap;margin:10px 10px 10px 30px;padding:30px;background:rgba(59,59,59,0.2);border:1px solid #424242;box-sizing:border-box;box-shadow:0px 0px 10px #000}#section-two-deals p{text-align:center}#sidebar{background:rgba(59,59,59,0.2);display:block;margin:10px;width:20vw;border:1px solid #424242;border-radius:4px;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;padding-bottom:5px;background:rgba(24,24,24,0.7);box-shadow:0px 0px 10px #000}#sidebar header{font-weight:bold;font-size:1.5rem;display:block;text-align:center;margin:5px;padding:10px;color:#eee}#sidebar a{display:block;border-width:1px;margin:5px 0 -5px 0;padding:10px;border-color:#5c5c5c transparent transparent;border-style:solid;color:#eee;text-decoration:none}#sidebar a:hover{background-color:rgba(0,0,0,0.3);color:#ffe657}.game-card{display:flex;flex-direction:column;justify-content:center;align-items:center;width:200px;height:290px;margin:10px 10px 40px;background:linear-gradient(to bottom right, rgba(165,165,165,0.2), rgba(48,48,48,0.2), rgba(197,197,197,0.2));background-size:200% 100%;background-position:50%;box-shadow:0px 0px 10px #000;border-radius:5px;border:1px solid #303030;cursor:pointer;transition:0.5s}.game-card #gameImage{width:130px;height:200px;margin:10px;background-size:cover;background-position:50% 0}.game-card p{display:inherit;justify-content:center;align-items:center;margin:0 10px 10px;width:75%;height:30px;text-shadow:0 0 4px #000}.game-card .btn{width:75%;height:30px;cursor:pointer;margin:0 10px 10px;display:flex;justify-content:center;align-items:center;font-family:"Roboto",sans-serif;font-size:1.1rem;font-weight:400;border-radius:3px;border:1px solid #424242;color:#eee;background-color:#00420f;transition:0.3s}.game-card .btn.btn:hover{background-color:#1d7531}.game-card .btn span{margin-right:1rem;-webkit-transform:translateY(1px);transform:translateY(1px)}.game-card .btn .fa{color:#eee}.game-card:hover{background-size:100% 100%;box-shadow:0 0 10px #000, inset 0 0 2px gray;transition:0.5s}#scroll-top{position:fixed;right:20px;bottom:20px;border-style:none;color:#ffe657;background:transparent}#scroll-top:hover{cursor:pointer}#scroll-top::after{content:'scroll to top';align-self:center;display:block;margin:10px;font-family:"Roboto",sans-serif;font-weight:500}@media screen and (min-width: 1200px){.App{max-width:1360px;margin:auto}}@media (max-width: 1200px){#main-deals{width:460px}}@media (max-width: 1000px){.section-two{flex-direction:column}#sidebar{width:100%;text-align:center;padding-bottom:20px;margin:0}#sidebar header{font-size:1rem}#sidebar a{font-size:0.8rem;display:inline-block;min-width:110px;margin:5px 0 -5px;border:1px solid #313131}#section-two-deals{width:100%;margin:20px 0;padding:60px 20px}}@media (max-width: 800px){#navbar{font-size:1.1rem;max-width:100vw;flex-wrap:wrap}.company{margin:20px 0;font-size:1.7rem}.company img{width:80px}.company p::after{font-size:1rem;margin:2px 0px 0 23px}#main-deals{display:block;width:auto;-webkit-transform:translateX(0);transform:translateX(0)}#banner{display:none}#headline{width:90vw;display:block;margin:auto}.main-sales{justify-content:space-between}.main-sales .game-card{width:28vw;overflow:hidden;margin:0;border:0}.main-sales .game-card #gameImage{max-height:200px;background-size:auto contain}#section-two-deals .game-card .btn{font-size:2vh}#section-two-deals .game-card .btn span{margin-right:0.25rem}}@media screen and (max-width: 570px){#headline h1{font-size:10vw}.main-sales .game-card{height:auto}.main-sales .game-card p{font-size:3.2vw;line-height:1.2}.main-sales .game-card .btn{font-size:3vw}.main-sales .game-card .btn span{margin-right:0.25rem}#section-two-deals{padding:5px}#section-two-deals .game-card{width:40%;padding:0;overflow:hidden}#section-two-deals .game-card #gameImage{background-size:auto contain}}@media (max-width: 400px){#navbar li a{font-size:5vw;padding:0.5rem}#section-two-deals .game-card{height:auto;margin-bottom:1rem;border:none;box-shadow:none}#section-two-deals .game-card p{padding:0.5rem 0;font-size:0.9rem;line-height:1.2}#section-two-deals .game-card .btn{padding:0.25rem;font-size:0.9rem}#section-two-deals .game-card .btn span{display:none}#section-two-deals .game-card #gameImage{width:80%;height:110px}}

